	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wonkette Disses Podcasters","date":"23 Mar 2005","slug":"/2005-03-23-wonkette-disses-podcasters/","tags":"Y2005"},"html":"<p><a href=\"http://www.wonkette.com/politics/democrats/index.php#today-millworkers-today-show-036850\" title=\"Today. Millworkers. Today Show. and Other Democrats Stories : Wonkette\">Wonkette:</a> \"The One America Committee, John Edwards's PAC, is venturing out into territory heretofore occupied only by losers with too much time on their hands (which, really, means this sort of makes sense): the Podcast.\" (File under \"Posted to provoke <a href=\"http://useful-sounds.de/\">Nicole</a>\")</p>"},"next":{"fields":{"title":"I'll Be The Envy Of The Office...","date":"23 Mar 2005","slug":"/2005-03-23-ill-be-the-envy-of-the-office/"}},"previous":{"fields":{"title":"Carstensen in Exile","date":"24 Mar 2005","slug":"/2005-03-24-carstensen-in-exile/"}}},{"node":{"fields":{"title":"I'll Be The Envy Of The Office...","date":"23 Mar 2005","slug":"/2005-03-23-ill-be-the-envy-of-the-office/","tags":"Y2005"},"html":"<p><img alt=\"bl_shirt.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/03/bl_shirt.jpg\" width=\"320\" height=\"232\" /></p>\n<p>in my <a href=\"http://www.bloglines.com/\">Bloglines</a> T-Shirt.</p>\n<p>And yes, that is a camera growing out of my ear.</p>"},"next":{"fields":{"title":"Borders and Tunnels","date":"23 Mar 2005","slug":"/2005-03-23-borders-and-tunnels/"}},"previous":{"fields":{"title":"Wonkette Disses Podcasters","date":"23 Mar 2005","slug":"/2005-03-23-wonkette-disses-podcasters/"}}},{"node":{"fields":{"title":"Borders and Tunnels","date":"23 Mar 2005","slug":"/2005-03-23-borders-and-tunnels/","tags":"Y2005"},"html":"<p>Two items from <a href=\"http://jrobb.mindplex.org/2005/03/23.html\" title=\"John Robb's Weblog\">John Robb's Weblog</a> today: one from the New York Times on <a href=\"http://www.nytimes.com/2005/03/23/international/americas/23mexico.html?ex=1269234000&en=b5d823dd65308c42&ei=5090&partner=rssuserland\" title=\"The New York Times - International - Americas - At Mexican Border, Tunnels, Vile River, Rusty Fence\">an elaborate tunnel discovered at the Mexican border</a>, and another on <a href=\"http://lashawnbarber.com/archives/2005/03/15/interviewi/\" title=\"La Shawn Barber's Corner &raquo; Interview with John Smallberries, Minuteman Project Volunteer\">a citizens' militia in Arizona</a> to patrol the border.</p>\n<p>This is related to <a href=\"http://useful-sounds.de/pivot/entry.php?id=160\" title=\"Then they came for the bloggers. | Useful Sounds - Nicole Simon's english [blog|podcast]\">Nicole Simon's indignation</a> at the treatment of legitimate travellers entering the United States. That policy, considering the ease with which drugs and terrorists can enter the country from Mexico, is a big sham. And as Nicole points out, people are indignant enough to not travel or work in the US at allow. That's tourist money, business, talent and knowledge that is permanently lost to the US.</p>\n<p>Meanwhile, according to the Times, 80 migrants per year are found dead on the US side of the fence at the Mexican border. Less than 200 Germans were killed during <a href=\"http://en.wikipedia.org/wiki/Berlin_wall#The_years_of_the_Wall\">the entire history of the Berlin Wall</a>. Which border is more dangerous?</p>"},"next":{"fields":{"title":"HOWTO: Walk to the shops with more than one small child","date":"22 Mar 2005","slug":"/2005-03-22-howto-walk-to-the-shops-with-more-than-one-small-child/"}},"previous":{"fields":{"title":"I'll Be The Envy Of The Office...","date":"23 Mar 2005","slug":"/2005-03-23-ill-be-the-envy-of-the-office/"}}}],"pathPrefix":"","first":false,"last":false,"index":334,"pageCount":1101}}

